import React, { Component } from 'react'
import _ from 'lodash'
import PropTypes from 'prop-types'

class BauerCreative extends Component {
  constructor(props) {
    super(props)
    this.iframeId = _.uniqueId('bauerCreativeFrame_')
    console.log('bauer creative mounted')
  }
  componentDidMount() {
    const jsCode = `var iframes = iFrameResize({}, '#${this.iframeId}')`
    new Function(jsCode)();
  }
  iframe () {
    let iframeId = _.uniqueId('bauerCreativeFrame_')
    return (
      <div className='embeds-bauercreative'>
        <script src='https://cdnjs.cloudflare.com/ajax/libs/iframe-resizer/3.5.14/iframeResizer.min.js'></script>
        <iframe src={this.props.data.url} allowtransparency='true' frameBorder='0' id={this.iframeId} scrolling='no' width='100%'></iframe>
      </div>
    )
    // <script>{`var iframes = iFrameResize({}, '##{iframeId}')`}</script>
  }
  render () {
    return (<div>{this.iframe()}</div>)
  }
}

BauerCreative.propTypes = {
  data: PropTypes.object
}

export default BauerCreative
